.resetfilters {
    margin-top: 90px;
    display: flex;
    justify-content: center;
}

.btnreset {
    background-color: rgba(0, 0, 0, 0);
    border-color: rgba(157, 185, 218, 0.644);
    color: white;
    padding: 7px;
}