.divCard {
    display: inline-table;
    background-color: rgba(0, 0, 0, 0.808);
    border-radius: 3px;
    font-size: 10px;
    line-height: 2px;
    margin: 10px;
    box-shadow: 1px 1px 5px 1px rgba(0, 0, 0, 0.705);
}

.name {
    color: silver;
    text-align: center;
}

.flag {
    width: 300px;
    height: 160px;
    display: block;
    margin: auto;
}

.continent {
    color: silver;
    text-align: center;
    font-size: 10px;
}